import { useCallback } from "react";
import { createMemo, updateMemo, deleteMemo } from "../api";
import { toast } from "react-hot-toast";

/**
 * Custom hooks to handle actions on memos (create, update, delete, pin, mark as done)
 */
export const useMemoActions = ({
  token,
  loadMemos,
  setLoading,
  setError,
  setEditingMemoId,
}) => {
  //  New memo creation process
  const handleCreate = useCallback(
    async (title, content, category, attachments = []) => {
      if (!token) {
        toast.error("");
        setError("");
        return;
      }

      setLoading(true);
      setError(null);

      try {
        const response = await createMemo(token, {
          title,
          content,
          category,
          attachments,
        });

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.message || "");
        }

        await loadMemos();
        toast.success("");
      } catch (err) {
        console.error(":", err);
        toast.error(err.message || "");
        setError(err.message || "");
      } finally {
        setLoading(false);
      }
    },
    [token, loadMemos, setLoading, setError]
  );

  //  Editing and updating memos
  const handleUpdate = useCallback(
    async (id, title, content, category, attachments = undefined) => {
      if (!token) {
        toast.error("");
        setError("");
        return;
      }

      setLoading(true);
      setError(null);

      try {
        const payload = {
          title,
          content,
          category,
        };

        // 
        if (attachments !== undefined) {
          payload.attachments = attachments;
        }

        const response = await updateMemo(token, id, payload);

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.message || "");
        }

        await loadMemos();
        setEditingMemoId(null);
        toast.success("");
      } catch (err) {
        console.error(":", err);
        toast.error(err.message || "");
        setError(err.message || "");
      } finally {
        setLoading(false);
      }
    },
    [token, loadMemos, setLoading, setError, setEditingMemoId]
  );

  //  Deleting memos (moving to trash)
  const handleDelete = useCallback(
    async (id) => {
      if (!token) {
        toast.error("");
        setError("");
        return;
      }

      setLoading(true);
      setError(null);

      try {
        const response = await deleteMemo(token, id);

        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(errorData.message || "");
        }

        await loadMemos();
        toast.success("");
      } catch (err) {
        console.error(":", err);
        toast.error(err.message || "");
        setError(err.message || "");
      } finally {
        setLoading(false);
      }
    },
    [token, loadMemos, setLoading, setError]
  );

  //  Switching the completion state (flipping isDone true/false)
  const handleToggleDone = useCallback(
    async (memo) => {
      if (!token) {
        toast.error("");
        setError("");
        return;
      }

      setLoading(true);
      setError(null);

      try {
        const response = await updateMemo(token, memo._id, {
          title: memo.title,
          content: memo.content,
          isDone: !memo.isDone,
        });

        if (!response.ok) {
          throw new Error("");
        }

        await loadMemos();
      } catch (err) {
        console.error(":", err);
        toast.error(err.message || "");
        setError(err.message || "");
      } finally {
        setLoading(false);
      }
    },
    [token, loadMemos, setLoading, setError]
  );

  //  Pin state switching process (flipping true/false of isPinned)
  const handleTogglePin = useCallback(
    async (memo) => {
      if (!token) {
        toast.error("");
        return;
      }

      setLoading(true);

      try {
        const response = await updateMemo(token, memo._id, {
          isPinned: !memo.isPinned,
        });

        if (!response.ok) {
          throw new Error("");
        }

        await loadMemos();
      } catch (err) {
        console.error(":", err);
        toast.error(err.message || "");
      } finally {
        setLoading(false);
      }
    },
    [token, loadMemos, setLoading]
  );

  //  Returns a set of functions to be used externally
  return {
    handleCreate,
    handleUpdate,
    handleDelete,
    handleToggleDone,
    handleTogglePin,
  };
};
